import WeakSet from '.WeakSet';

const arrays                 = new WeakSet;
export const isArray = (value     )                 => arrays.has(value);

export const OF_TABLES = false;
export const STATICALLY = true;
const staticalArrays                 = new WeakSet;
export const isStatic = (value       )          => staticalArrays.has(value);

export const newArray = (isStatic         )        => {
	const array        = [];
	arrays.add(array);
	isStatic && staticalArrays.add(array);
	return array;
};

                                 
	                  
	              
 
