import WeakSet from '.WeakSet';
import freeze from '.Object.freeze';
import Null from '.null';

import { Null as orderify_Null } from '@ltd/j-orderify';

const tables                 = new WeakSet;
export const isTable = (value     )                 => tables.has(value);

export const DIRECTLY = true;
export const IMPLICITLY = false;
const implicitTables                 = new WeakSet;
export const wasDirectly = (table       )          => !implicitTables.has(table);
export const directly = (table       )       => { implicitTables.delete(table); };

export const INLINE = true;
const inlineTables                 = new WeakSet;
export const isInline = (value       )          => inlineTables.has(value);

export const PAIR = true;
const pairs                 = new WeakSet;
export const fromPair = (value       )          => pairs.has(value);

export const PlainTable = class Table extends Null      {
	constructor (isDirect          , isInline          ) {
		super();
		tables.add(this);
		isDirect
			? isInline && inlineTables.add(this)
			: ( isInline ? pairs : implicitTables ).add(this);
		return this;
	}
};
delete PlainTable.prototype.constructor;
freeze(PlainTable.prototype);
freeze(PlainTable);

export const OrderedTable = class Table extends orderify_Null      {
	constructor (isDirect          , isInline          ) {
		super();
		tables.add(this);
		isDirect
			? isInline && inlineTables.add(this)
			: ( isInline ? pairs : implicitTables ).add(this);
		return this;
	}
};
delete OrderedTable.prototype.constructor;
freeze(OrderedTable.prototype);
freeze(OrderedTable);

                                       
                                
	                                               
	                                             
  
