import Error from '.Error';
import TypeError from '.TypeError';
import SyntaxError from '.SyntaxError';

//import * as options\$0 from './options\$0';

const NONE                = [];
let sourcePath         = '';
let sourceLines                = NONE;
let lastLineIndex         = -1;
export let lineIndex         = -1;

                                     
	                  
	                   
 
export const throws                                = (error             )        => {
	//if ( sourceLines!==NONE ) { done(); options\$0.clear(); }
	throw error;
};

const previous = Symbol('previous');
             
	                          
	                 
  
const noop       = ()         => '';
noop[previous] = noop;

export let stacks_length = 0;
let last       = noop;

export const could = ()       => {
	if ( sourceLines!==NONE ) { throw Error('Internal error: parsing during parsing.'); }
};

const EOL = /\r?\n/;
export const todo = (source        , path        )       => {
	if ( typeof path!=='string' ) { throw TypeError('TOML.parse(,,,,sourcePath)'); }
	sourcePath = path;
	sourceLines = source.split(EOL);
	lastLineIndex = sourceLines.length - 1;
	lineIndex = -1;
	stacks_length = 0;
	last = noop;
};

export const next = ()         => sourceLines[++lineIndex] ;

export const rest = ()          => lineIndex!==lastLineIndex;

export const mark = (type        ) => ( { type, lineIndex } );

export const must = (marker                                     )         => {
	lineIndex===lastLineIndex && throws(SyntaxError(`${marker.type} is not close until the end of the file` + where(', which started from ', marker.lineIndex)));
	return sourceLines[++lineIndex] ;
};

export const where = (pre        , index         = lineIndex)         => sourceLines===NONE ? '' :
	sourcePath
		? `\n    at (${sourcePath}:${index + 1}:1)`
		: `${pre}line ${index + 1}: ${sourceLines[index]}`;

export const done = ()       => {
	sourcePath = '';
	sourceLines = NONE;
	last = noop;
};

export const stacks_pop = ()       => {
	const item       = last;
	last = last[previous] ;
	--stacks_length;
	return item;
};

export const stacks_push = (item      )       => {
	item[previous] = last;
	last = item;
	++stacks_length;
};

export const stacks_insertBeforeLast = (item      )       => {
	item[previous] = last[previous];
	last[previous] = item;
	++stacks_length;
};
